<template>
  <div>
    <concordance :tokens="tokens" :total="total" :on-change="changePage"></concordance>
  </div>
</template>

<script>
import Concordance from './ui/Concordance'

export default {
  components: {
    Concordance
  },

  data () {
    return {
      page: 1,
      total: 0,
      tokens: [],
      _save: {}
    }
  },

  route: {
    data ({ to }) {
      this._save = to.query // FIXME

      return this.$http.get('tokens{?ids,source,language,form,lemma,morphology,part_of_speech,relation,information_status,person,number,tense,mood,voice,gender,case,degree,strength,inflection,page}', {
        params: {
          ids: to.query.ids,
          source: to.query.source,
          language: to.query.language,
          form: to.query.form,
          lemma: to.query.lemma,
          morphology: to.query.morphology,
          part_of_speech: to.query.part_of_speech,
          relation: to.query.relation,
          information_status: to.query.information_status,
          person: to.query.person,
          number: to.query.number,
          tense: to.query.tense,
          mood: to.query.mood,
          voice: to.query.voice,
          gender: to.query.gender,
          case: to.query.case,
          degree: to.query.degree,
          strength: to.query.strength,
          inflection: to.query.inflection,
          page: +to.query.page || 1
        }
      }).then((response) => {
        let payload = response.json()

        this.tokens = payload.data
        this.total = payload.total
      })
    }
  },

  methods: {
    changePage(page) {
      let newParams = this.$route.query
      newParams.page = +page || 1

      this.$router.go({
        name: 'tokens',
        query: newParams
      })
    }
  }
}
</script>



