<template>
  <div>
    <section>
      <table class="table">
        <thead>
          <tr>
            <th>Arguments</th>
            <th>Occurrences</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="frame in frames">
            <td v-if="frame.arguments.length > 0">
              <span v-for="argument in frame.arguments">
                {{ argument.relation.toUpperCase() }}
                <span v-if="argument.case">
                  ({{ argument.case | humanizeCase }})
                </span>
                <span v-if="argument.lemma">
                  (<em>{{ argument.lemma }}</em>, {{ argument.part_of_speech | humanizePartOfSpeech }})
                </span>
              </span>
            </td>
            <td v-else>
              (none)
            </td>
            <td>
              <a v-link="{ name: 'tokens',  query: { ids: frame.token_ids }}">{{ frame.token_ids.length }}</a>
            </td>
          </tr>
        </tbody>
      </table>
    </section>
  </div>
</template>

<script>
import Schema from '../schema'

export default {
  components: {
  },

  props: {
    frames: Array
  },

  filters: {
    humanizePartOfSpeech: function(partOfSpeech) {
      return Schema.humanizePartOfSpeech(window.store, partOfSpeech)
    },

    humanizeCase: function(kase) {
      return Schema.humanizeCase(window.store, kase)
    }
  }
}
</script>

<style lang="sass" scoped>
// tbody
//  border-top: 1px solid #aeb1b5
//  border-bottom: 1px solid #aeb1b5
</style>



