<template>
  <div>
    <h1 class="title">Dictionary</h1>
    <h2 class="subtitle">{{ language | humanizeLanguage }}</h2>

    <table class="table">
      <tbody>
        <tr v-for="lemma in dictionary">
          <td><a v-link="{ name: 'lemma', params: { language: language, lemma: lemma.id }}">{{ lemma.lemma }} ({{ lemma.part_of_speech | humanizePartOfSpeech }})</a> ‘{{ lemma.glosses.eng }}’</td>
        </tr>
      </tbody>
    </table>

    <pagination :per-page="20" :on-change="changePage" :total="total" :current-page="page"></pagination>
  </div>
</template>

<script>
import Schema from './schema'
import Pagination from './ui/Pagination'

export default {
  name: 'DictionaryView',

  components: {
    Pagination
  },

  data () {
    return {
      page: 1,
      total: 0,
      language: null,
      dictionary: []
    }
  },

  filters: {
    humanizePartOfSpeech: function(partOfSpeech) {
      return Schema.humanizePartOfSpeech(window.store, partOfSpeech)
    },

    humanizeLanguage: function(language) {
      return Schema.humanizeLanguage(window.store, language)
    }
  },

  route: {
    data ({ to }) {
      this.language = to.params.language
      this.page = +to.params.page || 1

      return this.$http.get('dictionaries/{language}/lemmas{?part_of_speech,page}', {
        params: {
          language: this.language,
          page: this.page,
          part_of_speech: to.query.part_of_speech
        }
      }).then((response) => {
        let payload = response.json()

        this.total = payload.total
        this.dictionary = payload.data
      })
    }
  },

  methods: {
    changePage(page) {
      let newQuery = this.$route.query

      this.$router.go({
        name: 'dictionary',
        params: {
          language: this.language,
          page: +page || 1
        },
        query: newQuery
      })
    }
  }
}
</script>



