import Vue from 'vue'
import Router from 'vue-router'
import Resource from 'vue-resource'
import NProgress from 'nprogress'

Vue.use(Router)
Vue.use(Resource)

Vue.http.interceptors.push((request, next) => {
  NProgress.inc(0.2)

  next((response) => {
    NProgress.done()
  })
})

// Store
const apiURL = 'http://foni.uio.no:9292/api/v1'
// const apiURL = 'http://shuttle:9292/api/v1'

var store = {
  state: {
    currentTreebank: {
      name: null,
      version: null
    },
    schema: {},
    dictionaries: [],
    sources: []
  },

  changeTreebank(name, version) {
    var self = this

    this.state.currentTreebank.name = name
    this.state.currentTreebank.version = version

    console.log("Loading schema...")
    Vue.http.options.root = apiURL + '/' + name + '/' + version

    Vue.http.get('schema', {
    }).then((response) => {
      // self.state.schema = response
    }, (response) => {
      // TODO
      console.log(response)
    })

    this.get(name, version, '/schema', function(d) { self.state.schema = d })
    this.get(name, version, '/dictionaries', function(d) { self.state.dictionaries = d })
    this.get(name, version, '/sources', function(d) { self.state.sources = d })
  },

  get(name, version, path, cb) {
    let url = apiURL

    if (name) {
      url += '/' + name
    }
    if (version) {
      url += '/' + version
    }
    url += path

    let xhr = new XMLHttpRequest()
    xhr.open('GET', url)
    xhr.onload = function () {
      var d = JSON.parse(xhr.responseText)
      cb(d)
    }
    xhr.send()
  }
}

store.changeTreebank('torot', '20160616') // FIXME
window.store = store // FIXME

const router = new Router({
  // history: true,
  saveScrollPosition: true,
  transitionOnLoad: true,
  linkActiveClass: 'is-active'
})

router.beforeEach(({ next }) => {
  NProgress.start()
  // if (sidebar.isMobile && sidebar.opened) {
  //   sidebar.opened = false
  // }
  next()
})

router.afterEach(() => {
  NProgress.done()
})

router.map({
  '/': {
    name: 'contents',
    component: require('./Contents.vue')
  },

  '/dictionary/:language/page/:page': {
    name: 'dictionary',
    component: require('./Dictionary.vue')
  },

  '/dictionary/:language/lemma/:lemma': {
    name: 'lemma',
    component: require('./Lemma.vue')
  },

  '/sentence/:sentence': {
    name: 'sentence',
    component: require('./Sentence.vue')
  },

  '/source/:source': {
    name: 'source',
    component: require('./Source.vue')
  },

  '/tokens': {
    name: 'tokens',
    component: require('./Tokens.vue')
  },

  '/query': {
    name: 'query',
    component: require('./Query.vue')
  },

  '/search': {
    name: 'search',
    component: require('./Search.vue')
  }
})

router.redirect({
  '/dictionary/:language': '/dictionary/:language/page/1',
  '*': '/dictionary/orv/page/1'
})

import App from './App'
router.start(App, 'app')



