<template>
  <div>
    <h1 class="title" language="{{ lemma.language }}">{{ lemma.lemma }}</h1>
    <h2 class="subtitle">{{ lemma.language | humanizeLanguage }}, {{ lemma.part_of_speech | humanizePartOfSpeech }}</h2>

    <div class="columns">
      <div class="column is-one-third">
        <aside class="menu">
          <p class="menu-label">General</p>
          <ul class="menu-list">
            <li><a @click.prevent="select('definition')" :class="{'is-active': selected == 'definition'}">Definition</a></li>
            <li><a @click.prevent="select('paradigm')" :class="{'is-active': selected == 'paradigm'}">Paradigm</a></li>
            <li><a @click.prevent="select('concordance')" :class="{'is-active': selected == 'concordance'}">Concordance</a></li>
            <li><a @click.prevent="select('timeline')" :class="{'is-active': selected == 'timeline'}">Timeline</a></li>
            <li><a @click.prevent="select('valency')" :class="{'is-active': selected == 'valency'}">Valency</a></li>
          </ul>
        </aside>
      </div>

      <div class="column">
        <div class="box" v-if="selected=='definition'">
          <div class="content" :lang="$key" v-for="gloss in lemma.glosses">
            {{ gloss }}
          </div>
        </div>

        <div class="container" v-if="selected=='paradigm'">
          <paradigm :paradigm="paradigm" :forms="forms" :lemma="lemma"></paradigm>
        </div>

        <div class="container" v-if="selected=='concordance'">
          <concordance :tokens="tokens" :total="total" :on-change="changePage"></concordance>
        </div>

        <div class="container" v-if="selected=='timeline'">
          <p class="control">
            <label class="radio">
              <input type="radio" value="text" v-model="timelineSelected">
              Text
            </label>
            <label class="radio">
              <input type="radio" value="ms" v-model="timelineSelected">
              Manuscript
            </label>
          </p>
          <timeline :timeline="timeline"></timeline>
        </div>

        <div class="container" v-if="selected=='valency'">
          <valency :frames="valency"></valency>
        </div>
      </div>
    </div>

    <!-- h3 class="subtitle is-7">Homographs</h3>

    <div class="box" v-for="homograph in lemma.homographs">
