      </div>

      <div class="valency box">
        <div class="content">
          <p><strong>Subject:</strong> noun, nominative (2); noun, dative (1); pronoun, <em>онъ</em>, nominative (1); pronoun, <em>ты</em>, nominative (1); conjunction (2)</p>
          <p><strong>Object:</strong> noun, accusative (1); noun, genitive (2); pronoun, <em>вы</em>, genitive (1); pronoun, <em>и</em>, genitive (1); pronoun, <em>тыи</em>, genitive (1); adjective, genitive (1); verb, participle, genitive (1)</p>
