</template>

<script>
import Schema from './schema'
import Concordance from './ui/Concordance'
import Timeline from './ui/Timeline'
import Paradigm from './ui/Paradigm'
import Valency from './ui/Valency'

import Tabs from './ui/fundon/Tabs'
import TabPane from './ui/fundon/TabPane'

export default {
  name: 'LemmaView',

  components: {
    Paradigm,
    Concordance,
    Timeline,
    Valency,
    Tabs,
    TabPane
  },

  data () {
    return {
      timelineSelected: 'text',
      selected: 'definition',
      lemma: {
        paradigm: {}
      },
      tokens: [],
      total: 0,
      paradigms: {
        "A-": [
          {
            title: 'Masculine',
            levels1: [
              { text: 'Singular' },
              { text: 'Dual' },
              { text: 'Plural' }
            ],
            levels2: [
              { text: 'Nom.', patterns: ['.s...mn...', '.d...mn...', '.p...mn...'] },
              { text: 'Acc.', patterns: ['.s...ma...', '.d...ma...', '.p...ma...'] },
              { text: 'Gen.', patterns: ['.s...mg...', '.d...mg...', '.p...mg...'] },
              { text: 'Loc.', patterns: ['.s...ml...', '.d...ml...', '.p...ml...'] },
              { text: 'Dat.', patterns: ['.s...md...', '.d...md...', '.p...md...'] },
              { text: 'Ins.', patterns: ['.s...mi...', '.d...mi...', '.p...mi...'] }
            ]
          },

          {
            title: 'Feminine',
            levels1: [
              { text: 'Singular' },
              { text: 'Dual' },
              { text: 'Plural' }
            ],
            levels2: [
              { text: 'Nom.', patterns: ['.s...fn...', '.d...fn...', '.p...fn...'] },
              { text: 'Acc.', patterns: ['.s...fa...', '.d...fa...', '.p...fa...'] },
              { text: 'Gen.', patterns: ['.s...fg...', '.d...fg...', '.p...fg...'] },
              { text: 'Loc.', patterns: ['.s...fl...', '.d...fl...', '.p...fl...'] },
              { text: 'Dat.', patterns: ['.s...fd...', '.d...fd...', '.p...fd...'] },
              { text: 'Ins.', patterns: ['.s...fi...', '.d...fi...', '.p...fi...'] }
            ]
          },

          {
            title: 'Neuter',
            levels1: [
              { text: 'Singular' },
              { text: 'Dual' },
              { text: 'Plural' }
            ],
            levels2: [
              { text: 'Nom.', patterns: ['.s...nn...', '.d...nn...', '.p...nn...'] },
              { text: 'Acc.', patterns: ['.s...na...', '.d...na...', '.p...na...'] },
              { text: 'Gen.', patterns: ['.s...ng...', '.d...ng...', '.p...ng...'] },
              { text: 'Loc.', patterns: ['.s...nl...', '.d...nl...', '.p...nl...'] },
              { text: 'Dat.', patterns: ['.s...nd...', '.d...nd...', '.p...nd...'] },
              { text: 'Ins.', patterns: ['.s...ni...', '.d...ni...', '.p...ni...'] }
            ]
          }
        ],

        'Nb': [
          {
            title: '',
            levels1: [
              { text: 'Singular' },
              { text: 'Dual' },
              { text: 'Plural' }
            ],
            levels2: [
              { text: 'Nom.', patterns: ['.s....n...', '.d....n...', '.p....n...'] },
              { text: 'Acc.', patterns: ['.s....a...', '.d....a...', '.p....a...'] },
              { text: 'Gen.', patterns: ['.s....g...', '.d....g...', '.p....g...'] },
              { text: 'Loc.', patterns: ['.s....l...', '.d....l...', '.p....l...'] },
              { text: 'Dat.', patterns: ['.s....d...', '.d....d...', '.p....d...'] },
              { text: 'Ins.', patterns: ['.s....i...', '.d....i...', '.p....i...'] }
            ]
          }
        ]
      }
    }
  },

  computed: {
    // FIXME
    forms() {
      return this.lemma.paradigm
    },

    valency() {
      return this.lemma.valency
    },

    paradigm() {
      return this.paradigms[this.lemma.part_of_speech]
    },

    timeline() {
      let data = []

      for (var i in this.lemma.distribution) {
        var d = this.lemma.distribution[i]
        if (this.timelineSelected === 'ms') {
          data.push({
            Lemma: this.lemma.lemma,
            Text: d.id,
            Frequency: d.n,
            Year: d.chronology.ms
          })
        } else {
          data.push({
            Lemma: this.lemma.lemma,
            Text: d.id,
            Frequency: d.n,
            Year: d.chronology.text
          })
        }
      }

      return data
    }
  },

  route: {
    data ({ to }) {
      const language = to.params.language
      const lemma = +to.params.lemma

      return this.$http.get('dictionaries/{language}/lemmas/{lemma}', {
        params: {
          language: language,
          lemma: lemma
        }
      }).then((response) => {
        let payload = response.json()
        this.lemma = payload

        return this.changePage(1)
      })
    }
  },

  methods: {
    select(s) {
      this.selected = s
    },

    changePage(page) {
      return this.$http.get('tokens?language={language}&lemma={lemma}&part_of_speech={pos}&page={page}', {
        params: {
          language: this.lemma.language,
          lemma: this.lemma.lemma,
          pos: this.lemma.part_of_speech,
          page: +page
        }
      }).then((response) => {
        let payload = response.json()

        this.tokens = payload.data
        this.total = payload.total

        return {}
      })
    }
  },

  // created () {
  //   this.fetchData(this.$route.params.language,
  //                  this.$route.params.lemma)
  // },

  filters: {
    humanizePartOfSpeech: function(partOfSpeech) {
      return Schema.humanizePartOfSpeech(window.store, partOfSpeech)
    },

    humanizeLanguage: function(language) {
      return Schema.humanizeLanguage(window.store, language)
    }
  }
}
</script>



