<template>
  <div>
    <div v-for="p in paradigm">
      <h3 class="subtitle is-7"><em>{{ p.title }}</em></h3>

      <table class="table">
        <thead>
          <tr>
            <th></th>
            <th width="30%" v-for="l1 in p.levels1">{{ l1.text }}</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="l2 in p.levels2">
            <th>{{ l2.text }}</th>
            <td v-for="pattern in l2.patterns">
              <span v-for="form in getParadigmForms(pattern)">
                <a v-link="{ name: 'tokens', query: { form: form.form, part_of_speech: lemma.part_of_speech, lemma: lemma.lemma }}">{{ form.form }}</a> ({{ form.n }})
              </span>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    lemma: Object,
    forms: Object,
    paradigm: Array
  },

  methods: {
    getParadigmForms(pattern) {
      let regex = new RegExp('^' + pattern + '$')
      let matches = []

      for (let m in this.forms) {
        if (this.forms.hasOwnProperty(m)) {
          if (regex.test(m)) {
            let forms = this.forms[m]
            for (let f in forms) {
              if (forms.hasOwnProperty(f)) {
                matches.push({ form: f, n: forms[f] })
              }
            }
          }
        }
      }

      return matches
    }
  }
}
</script>

<style lang="sass" scoped>
th:first-child, tbody th
  border-right: 2px solid #d3d6db
</style>




